import { Error, SvgIconComponent } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { logError } from '../helpers/logger'
import { Colors, Spacing } from '../styling'
import { Icon } from './Icon'

interface IProps {
  icon?: SvgIconComponent
  label?: string
  labelWidth?: number
  labelWidthUnits?: string
  labelMargin?: string
  className?: string
}

const StyledIconLine = styled.div<IProps>`
  display: flex;

  div.description {
    margin: ${({ labelMargin }) => labelMargin};
    min-width: ${({ labelWidth, labelWidthUnits }) =>
      `${labelWidth}${labelWidthUnits}`};
    flex: 0 0 auto;
    text-align: right;
  }

  div.contents {
    margin-left: 0.5rem;
    align-self: stretch;
    flex: 1 1 auto;
  }

  div.label {
    padding-top: 0.2rem;
    display: inline-block;
  }
`

export const IconLine: React.FC<IProps> = ({
  icon,
  label,
  labelWidth = 0,
  labelWidthUnits = Spacing.DefaultSizeUnit,
  labelMargin = '0.5rem 0.2rem 0 auto',
  children,
}) => {
  let iconComponent = icon
  if (typeof icon === 'string') {
    logError(
      `IconLine: Do not use icon prop as string! Replace "${icon}" with the one imported from @material-ui/icons.`,
    )
    iconComponent = Error
  }

  return (
    <StyledIconLine
      labelWidth={labelWidth}
      labelWidthUnits={labelWidthUnits}
      labelMargin={labelMargin}
    >
      <div className="description">
        <Icon icon={iconComponent} color={Colors.greyDark} />
        {label ? <div className="label">{label}</div> : null}
      </div>
      <div className="contents">{children}</div>
    </StyledIconLine>
  )
}
