import { Delete } from '@material-ui/icons'
import React from 'react'

import { SizerButton } from '../atoms/SizerButton'
import { Colors } from '../styling'
import { ITouchButtonProps, TouchButton } from './TouchButton'

interface ITouchButtonDeleteProps
  extends LegacyOmit<ITouchButtonProps, 'onClick'> {
  confirmText?: string
  onDelete(): void
}

export const TouchButtonDelete: React.FC<ITouchButtonDeleteProps> = ({
  onDelete,
  confirmText,
  ...props
}) => (
  <SizerButton>
    <TouchButton
      icon={Delete}
      color={Colors.greyMiddle}
      onClick={ev => {
        ev.preventDefault()
        if (!confirmText || window.confirm(confirmText)) {
          onDelete()
        }
      }}
      {...props}
    />
  </SizerButton>
)
