import { DFP, RM } from '@speedlo/tools'
import { IObservableArray, observable } from 'mobx'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { Loading } from '../../../atoms/Loading'
import { MarginBox } from '../../../atoms/MarginBox'
import { useRoot } from '../../../atoms/RootProvider'
import { Sizer } from '../../../atoms/Sizer'
import {
  InputBusinessHourCreate,
  useMBusinessHoursSubmit,
  useQRegularBusinessHours,
  WeekDayEnum,
} from '../../../graph/generated'
import { formatTime } from '../../../helpers/formats'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { TBusinessHour } from '../businesshours.types'
import { BusinessHoursWeekday } from '../molecules/BusinessHoursWeekday'

const getTodayTime = DFP.parse(new Date(), 'H:mm')

export const BusinessHoursForm: React.FC = () =>
  useObserver(function useObserverBody() {
    const { settings } = useRoot()
    const { data, loading } = useQRegularBusinessHours({
      branchId: settings.branchId,
    })

    const [businessHours] = React.useState(() =>
      observable(new Map<WeekDayEnum, IObservableArray<TBusinessHour>>()),
    )

    const [submitBusinessHours] = useMBusinessHoursSubmit()

    React.useEffect(() => {
      if (loading) {
        return
      }
      if (businessHours.size === 0) {
        data.weekDays.forEach(weekday => {
          businessHours.set(weekday.enum, observable.array([]))
        })
      }
      data.businessHours.forEach(({ id, openAt, closeAt, weekDay }) => {
        if (weekDay) {
          businessHours.get(weekDay.enum)!.push({
            id,
            openAt: getTodayTime(openAt),
            closeAt: getTodayTime(closeAt),
            weekDay: weekDay.enum,
          })
        }
      })
      return () => {
        businessHours.forEach(weekDayHours => {
          weekDayHours.clear()
        })
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data.businessHours, data.weekDays, loading, settings.branchId])

    const hourIdCounter = React.useRef(0)

    const onAdd = (weekDay: WeekDayEnum) => () => {
      businessHours.get(weekDay)!.push({
        weekDay,
        id: `L${hourIdCounter.current++}`,
        openAt: DFP.startOfDay(new Date()),
        closeAt: DFP.endOfDay(new Date()),
      })
    }

    const onRemove = (weekDay: WeekDayEnum) => (hourId: ID) => {
      const hours = businessHours.get(weekDay)!
      const idx = hours.findIndex(RM.propEq('id', hourId))
      if (idx >= 0) {
        hours.splice(idx, 1)
      }
    }

    const { enqueueSuccess } = useSnackBar()

    const onSubmit = React.useCallback(async () => {
      const hours: InputBusinessHourCreate[] = []
      businessHours.forEach((weekDayHours, weekDay) => {
        hours.push(
          ...weekDayHours.map(hour => ({
            openAt: formatTime(hour.openAt),
            closeAt: formatTime(hour.closeAt),
            weekDay,
          })),
        )
      })
      const result = await submitBusinessHours({
        branchId: settings.branchId,
        hours,
      })
      if (result) {
        enqueueSuccess(i18n.t`Regular business hours has been updated`)
      }
    }, [businessHours, enqueueSuccess, settings.branchId, submitBusinessHours])

    if (loading || businessHours.size === 0) {
      return <Loading />
    }

    return (
      <div>
        {data.weekDays.map(weekday => (
          <BusinessHoursWeekday
            key={weekday.enum}
            weekday={weekday}
            hours={businessHours.get(weekday.enum)!}
            onAdd={onAdd(weekday.enum)}
            onRemove={onRemove(weekday.enum)}
          />
        ))}
        <Sizer width={20}>
          <MarginBox margin="1rem">
            <TouchButton
              text={i18n.t`Save regular hours`}
              color={Colors.green}
              onClick={onSubmit}
            />
          </MarginBox>
        </Sizer>
      </div>
    )
  })
