import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { NoAccess } from './NoAccess'
import { useRoot } from './RootProvider'

interface IProps {
  queryName: string
}

export const CanQuery: React.FC<IProps> = ({ queryName, children }) => {
  const { user } = useRoot()
  return useObserver(() => {
    if (user.canQuery(queryName)) {
      return <>{children}</>
    }
    return <NoAccess>{`No access to query ${queryName}`}</NoAccess>
  })
}
