import React from 'react'
import { Checkbox, FieldType } from 'react-form'

import { StyledCheckbox } from './StyledCheckbox'

interface IProps extends React.InputHTMLAttributes<never> {
  field: FieldType
  label?: string
}

export const FormInputCheckbox: React.FC<IProps> = ({
  field,
  label,
  defaultValue,
  children,
  ...inputProps
}) => (
  <StyledCheckbox>
    <Checkbox field={field} checked={Boolean(defaultValue)} {...inputProps} />
    {label && <label>{label}</label>}
  </StyledCheckbox>
)
