import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import { GSpecificBusinessHour } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { SpecificBusinessHourLine } from '../molecules/SpecificBusinessHourLine'
import { QuerySpecificBusinessHours } from './SpecificBusinessHours.graphql'

const sortByDate = RM.sort<GSpecificBusinessHour.Fragment>(
  // @ts-ignore Fix typing
  RM.ascend(RM.or(RM.prop('specificDate'), RM.not)),
)

export const SpecificBusinessHours: React.FC = () => {
  const { settings } = useRoot()

  return useObserver(() => (
    <QuerySpecificBusinessHours
      variables={{
        branchId: settings.branchId,
      }}
      fetchPolicy="cache-and-network"
      renderFragment={({ businessHours }) =>
        sortByDate(businessHours).map(bsHour => (
          <SpecificBusinessHourLine
            key={bsHour.id}
            bsHour={bsHour}
            onDelete={() =>
              settings.businessHours.deleteSpecificHour(bsHour.id)
            }
          />
        ))
      }
    />
  ))
}
