import { TimePickerProps } from '@material-ui/pickers'
import React from 'react'
import { Field, FieldType } from 'react-form'

import { InputTime } from './InputTime'

interface IProps extends LegacyOmit<TimePickerProps, 'value' | 'onChange'> {
  field: FieldType
}

export const FormInputTime: React.FC<IProps> = ({ field, ...pickerProps }) => {
  return (
    <Field
      field={field}
      render={fieldApi => (
        <InputTime
          value={fieldApi.value}
          onChange={value => fieldApi.setValue(value)}
          onOpen={() => fieldApi.setTouched(true)}
          {...pickerProps}
        />
      )}
    />
  )
}
