import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { NoAccess } from './NoAccess'
import { useRoot } from './RootProvider'

interface IProps {
  mutationName: string
}

export const CanMutate: React.FC<IProps> = ({ mutationName, children }) => {
  const { user } = useRoot()
  return useObserver(() => {
    if (user.canMutate(mutationName)) {
      return <>{children}</>
    }
    return <NoAccess>{`No access to mutation ${mutationName}`}</NoAccess>
  })
}
